<template>
  <el-form ref="user" :model="user" :rules="rules" status-icon>
    <el-form-item label="用户名" prop="name">
      <el-input v-model.trim="user.name" />
    </el-form-item>
    <el-form-item label="email" prop="email">
      <el-input v-model.trim="user.email" />
    </el-form-item>
    <el-form-item label="手机号" prop="mobile">
      <el-input v-model.trim="user.mobile" />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit">更新</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { isUserName, isMobile } from "@/utils/validate.js";
import { update } from "@/api/user.js";
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {
          name: "",
          email: "",
          mobile: ""
        };
      }
    }
  },
  data() {
    const checkName = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("用户名不能为空"));
      }
      if (value.length < 6 || value.length > 12 || !isUserName(value)) {
        return callback(
          new Error("用户名由6-12位的英文开头的英文和数字的组合")
        );
      }
      callback();
    };
    const checkMobile = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error("手机不能为空"));
      } else {
        if (isMobile(value)) {
          callback();
        } else {
          return callback(new Error("请输入有效的手机号"));
        }
      }
    };
    return {
      rules: {
        name: [{ validator: checkName, trigger: "blur" }],
        mobile: [{ validator: checkMobile, trigger: "blur" }]
      }
    };
  },
  methods: {
    submit() {
      update(this.user).then(response => {
        if (response.code === 0) {
          this.$message({
            message: "更新成功",
            type: "success",
            duration: 5 * 1000
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
      });
    }
  }
};
</script>
