<template>
  <el-form ref="user" :model="user" :rules="rules" status-icon>
    <el-form-item label="旧密码" prop="oldPassword">
      <el-input
        type="password"
        v-model.trim="user.oldPassword"
        ref="oldPassword"
      />
    </el-form-item>
    <el-form-item label="新密码" prop="password">
      <el-input type="password" v-model.trim="user.password" ref="password" />
    </el-form-item>
    <el-form-item label="确认密码" prop="checkPass">
      <el-input type="password" v-model.trim="user.checkPass" ref="checkPass" />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit">更新</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { isPsw } from "@/utils/validate.js";
import { changePassword } from "@/api/user.js";
export default {
  props: {
    user: {
      type: Object,
      default: () => {
        return {
          oldPassword: "",
          password: "",
          checkPass: ""
        };
      }
    }
  },
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (!isPsw(value)) {
          callback(
            new Error("密码长度6-18位，数字、字母、字符至少包含其中两种")
          );
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.user.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      rules: {
        oldPassword: [{ validator: validatePass, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    submit() {
      this.$refs.user.validate(valid => {
        if (valid) {
          changePassword(this.user).then(response => {
            if (response.code === 0) {
              this.user.password = "";
              this.user.oldPassword = "";
              this.user.newPassword = "";
              this.$message({
                message: "更新成功",
                type: "success",
                duration: 5 * 1000
              });
            } else {
              this.$message({
                message: response.message,
                type: "warning"
              });
            }
          });
        }
      });
    }
  }
};
</script>
